//Import images
import eye from "../assets/eye-outline.svg";
import closeEye from "../assets/eye-off-outline.svg";
//Import styles
import "../styles/style.scss";

const toggleShowPassword = () => {
    const imgEye = document.querySelector(".auth-form__eye");

    const observeAction = () => {
        if (imgEye.classList.contains("eye_close")) {
            document.querySelectorAll(".auth-form__input")[1].type = "text"
        } else {
            document.querySelectorAll(".auth-form__input")[1].type = "password"
        }
    };

    imgEye.src = eye;

    imgEye.addEventListener("click", () => {
        if (imgEye.classList.contains("eye_close")) {
            imgEye.classList.remove("eye_close");
            imgEye.src = eye;
        } else {
            imgEye.classList.add("eye_close");
            imgEye.src = closeEye;
        }
        observeAction();
    });
};

toggleShowPassword();